<template>
    <div class="main">
        <div v-wechat-title="$route.meta.title"></div>
        <!-- 修改密码 -->
        <div class="back" @click="goBack()">
            <img src="../img/left.png" alt="">
            <span>返回</span>
        </div>
        <div class="userInfo">
			<span>
				原始密码
			</span>
            <div class="userPhone">
                <img src="../img/password.png" alt="">
                <input type="number" placeholder="请输入原始密码" v-model="oldPwd">
            </div>
            <span class="none">
				新密码
			</span>
            <div class="password">
                <img src="../img/password.png" alt="">
                <input type="text" placeholder="请输入新密码" @change="check()" v-model="newPwd">
            </div>
            <p class="tip1" v-if="isCheck">密码至少6位字符</p>
            <span>
				确认新密码
			</span>
            <div class="userPhone">
                <img src="../img/password.png" alt="">
                <input type="number" placeholder="请确认新密码" v-model="surePwd">
            </div>
            <div class="reg">
                <button @click="edit()">确认修改</button>
            </div>

        </div>
    </div>

</template>

<script>
    import request from '../../../../static/js/request';
    import request1 from '../../../../static/js/request1';
    import result from '../../../../static/js/result1';

    export default {
        data() {
            return {
                phone: '',
                oldPwd: '',
                newPwd: '',
                surePwd: '',
                pwd: '',
                isCheck:false
            }
        },
        mounted() {

        },
        created() {
            //this.getUser()
        },
        methods: {
            check(){
                if(this.newPwd.length<6){
                    this.isCheck = true
                }else{
                    this.isCheck = false
                }
            },
            goBack() {
                this.$router.go(-1)
            },
            edit() {
                let url = ''
                if (this.$store.state.role == 2) {
                    url = '/api/token/user/updateUserStudent'
                } else if (this.$store.state.role == 3) {
                    url = '/api/token/user/updateUserParent'
                }
                let _this = this;
                if (_this.oldPwd == '') {
                    weui.alert("请输入原始密码！");
                    return false;
                }
                if (_this.oldPwd != _this.$route.query.oldPwd) {
                    weui.alert("原始密码输入错误！");
                    return false;
                }
                if (_this.newPwd == '') {
                    weui.alert("请输入新密码！");
                    return false;
                }
                if (_this.newPwd.length<6) {
                    weui.alert("密码至少为6位字符！");
                    return false;
                }
                if (_this.surePwd == '') {
                    weui.alert("请确认新密码！");
                    return false;
                }
                if (_this.surePwd != _this.newPwd) {
                    weui.alert("两次密码输入不一致！");
                    return false;
                }
                weui.dialog({
                    title: '确认修改密码吗',
                    content: '将密码号修改为' + _this.newPwd,
                    className: 'custom-classname',
                    buttons: [{
                        label: '取消',
                        type: 'default',
                        onClick: function () {
                            weui.toast('已取消修改', 2000);
                        }
                    }, {
                        label: '确定',
                        type: 'primary',
                        onClick: async function () {

                            let params = {
                                access_token: _this.$store.state.token,
                            }
                            if (_this.$store.state.role == 2) {
                                params.studentPassword = _this.newPwd;
                                params.studentId = _this.$route.query.studentId;
                            } else if (_this.$store.state.role == 3) {
                                params.parentPassword = _this.newPwd;
                                params.parentId = _this.$route.query.parentId;
                            }
                            let loading = weui.loading('loading', {
                                className: 'custom-classname'
                            });
                            let edit = await request(url,
                                'post', params, 'application/x-www-form-urlencoded')
                            let res = result(edit)
                            loading.hide()
                            if (!res) return;
                            console.log('结果', edit)
                            weui.toast('修改成功', 2000);
                            _this.$router.go(-1)
                        }
                    }]
                });

            },

        },
        watch: {}
    }
</script>

